import React from "react";
import {
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  FormText,
  Container,
  Row,
  Col,
} from "reactstrap";
import { colors } from "../../styles/styles";
import FullHeight from "react-full-height";

function BookARidePage(props) {
  return (
    <div style={{ height: "100%", width: "100%" }}>
      <Row>
        {/*----------- map col start ---------------*/}
        <Col
          xl={{ size: "8", order: 2 }}
          lg={{ size: "7", order: 2 }}
          md={{ size: "6", order: 1 }}
          xs={{ size: "12", order: 2 }}
          style={mapContainer}
        >
          <h1>map here</h1>
        </Col>
        {/*---------------- map col end---------- */}

        {/*--------------- details form start.-------*/}
        <Col
          xl={{ size: "4", order: 2 }}
          lg={{ size: "5", order: 2 }}
          md={{ size: "6", order: 2 }}
          xs={{ size: "12", order: 1 }}
          style={detailsStyle.container}
        >
          {/* this full height is a resposive that take the full height for 
            all browsers. all heavy lifting done */}
          <FullHeight canExceed className="col-12">
            <Row style={detailsStyle.header}>
              <h5 style={detailsStyle.headerText}>Booking Specification</h5>
            </Row>
            {/* service name */}
            <div style={flex}>
              <Row style={detailsStyle.inputWrapper}>
                <Label>Service Name</Label>
                <Input
                  type="select"
                  name="select"
                  id="exampleSelect"
                  style={detailsStyle.input}
                >
                  <option>Waradrive(Self drive)</option>
                  <option>Chauffeur-driven</option>
                  <option>Finaced-lease</option>
                </Input>
              </Row>
              {/* service type */}
              <Row style={detailsStyle.inputWrapper}>
                <Label>Service Type</Label>
                <Input
                  type="select"
                  name="select"
                  id="exampleSelect"
                  style={detailsStyle.input}
                >
                  <option>Whole Day (Accra)</option>
                  <option>Whole Day (Outside Accra)</option>
                  <option>Hourly</option>
                  <option>Taxi Service</option>
                </Input>
              </Row>
              {/* pickup location */}
              <Row style={detailsStyle.inputWrapper}>
                <Input
                  type="text"
                  name="pickupLocation"
                  placeholder="Pickup Location"
                  style={detailsStyle.inputStyle}
                />
              </Row>
              {/* drop off location */}
              <Row style={detailsStyle.inputWrapper}>
                <Input
                  type="text"
                  name="dropoffLocation"
                  placeholder="Drop-off Location"
                  style={detailsStyle.inputStyle}
                />
              </Row>
              {/* pickup date */}
              <Row style={detailsStyle.inputWrapper}>
                <Label for="pickupDate">Pickup Date</Label>
                <Input
                  type="date"
                  name="pickupDate"
                  placeholder="date placeholder"
                  style={detailsStyle.inputStyle}
                />
              </Row>
              {/* dropoff date */}
              <Row style={detailsStyle.inputWrapper}>
                <Label for="dropoffDate">Drop-off Date</Label>
                <Input
                  type="date"
                  name="dropoffDate"
                  placeholder="date placeholder"
                  style={detailsStyle.inputStyle}
                />
              </Row>
              {/* pickup time */}
              <Row style={detailsStyle.inputWrapper}>
                <Label for="pickupTime">Pickup Time</Label>
                <Input
                  type="time"
                  name="pickupTime"
                  placeholder="time placeholder"
                  style={detailsStyle.inputStyle}
                />
              </Row>
              {/* dropoff time */}
              <Row style={detailsStyle.inputWrapper}>
                <Label for="dropoffTime">Drop-off Time</Label>
                <Input
                  type="time"
                  name="dropoffTime"
                  placeholder="time placeholder"
                  style={detailsStyle.inputStyle}
                />
              </Row>
              {/* number of hours */}
              <Row style={detailsStyle.inputWrapper}>
                <Label>Number of hours</Label>
                <Input
                  type="select"
                  name="select"
                  id="exampleSelect"
                  style={detailsStyle.input}
                >
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                  <option>6</option>
                </Input>
              </Row>
              {/* additional information */}
              <Row style={detailsStyle.inputWrapper}>
                <Label for="additionalInformation">
                  Additional Information
                </Label>
                <Input type="textarea" name="additinalInformation" />
              </Row>
              {/* buton */}
              <Row style={detailsStyle.buttonWrapper}>
                <Button
                  style={detailsStyle.button}
                  href="/user/available-cars"
                >
                  continue
                </Button>
              </Row>
            </div>
          </FullHeight>
        </Col>
        {/*------------------------ detials form end--------------------------- */}
      </Row>
    </div>
  );
}

const mapContainer = {
  // borderStyle: "solid",
  height: "100%",
};

const flex = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
};

const detailsStyle = {
  container: {
    // borderStyle: "solid",
    height: "100%",
    backgroundColor: colors.bg,
    display: "flex",
    flexDirection: "column",
    // alignItems: "center",
  },
  header: {
    padding: 10,
    paddingLeft: 0,
    paddingRight: 0,
    borderStyle: "solid",
    borderTopWidth: 0,
    borderBottomWidth: 0.5,
    borderLeftWidth: 0,
    borderRightWidth: 0,
    justifyContent: "center",
    borderColor: "#c4c4c4",
  },
  headerText: {
    margin: 0,
    fontSize: 18,
  },
  inputWrapper: {
    // borderStyle: "solid",
    paddingTop: 5,
    paddingBottom: 5,
    paddingLeft: 15,
    paddingRight: 15,
    marginTop: 5,
    width: "95%",
  },

  input: {
    borderRadius: 100,
    alignSelf: "center",
  },
  inputStyle: {
    borderRadius: 100,
    paddingLeft: 10,
  },
  row: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    // borderStyle: "solid",
    width: "100%",
  },
  buttonWrapper: {
    marginTop: 20,
    marginBottom: 20,
    width: "80%",
  },
  button: {
    width: "100%",
    borderRadius: 100,
  },
};

export default BookARidePage;
