import React from "react";
import {
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Container,
  CustomInput,
  Row,
  Col,
} from "reactstrap";
import { colors } from "../../styles/styles";

function ProfilePage(props) {
  return (
    <Container fluid style={container}>
      <Col xl="8" lg="8" md="10" style={content}>
        <Row style={{ ...header, ...flex }}>
          <h5 style={headerText}>Password Update</h5>
        </Row>

        <Row style={{ ...body, ...flex }}>
          <Form>
            <FormGroup>
              <Input
                type="text"
                placeholder="New Password"
                style={inputStyle}
              />
            </FormGroup>
            <FormGroup>
              <Input
                type="text"
                placeholder="Confirm Password"
                style={inputStyle}
              />
            </FormGroup>

            <Button style={buttonStyle2}>Update</Button>
          </Form>
        </Row>
        <Row style={{ ...header, ...flex }}>
          <h5 style={headerText}>Licence Update</h5>
        </Row>
        <Row style={{ ...body, ...flex }}>
          <Form style={{ display: "grid", placeItems: "center" }}>
            <FormGroup style={{ width: "70%" }}>
              <Input type="text" placeholder="License ID" style={inputStyle} />
            </FormGroup>
            <FormGroup style={{ width: "70%" }}>
              <CustomInput
                type="file"
                id="exampleCustomFileBrowser"
                name="customFile"
              />
            </FormGroup>

            <Button style={buttonStyle}>Update</Button>
          </Form>
        </Row>
      </Col>
    </Container>
  );
}

const flex = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
};

const container = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  height: "100vh",
};

const content = {
  borderStyle: "solid",
  borderWidth: 1,
  borderColor: "#c4c4c4",
};

const header = {
  backgroundColor: colors.bg,
  padding: 10,
};

const headerText = {
  margin: 0,
  fontSize: "20",
  textAlign: "center",
};

const body = {
  padding: 20,
  width: "100%",
};

const inputStyle = {
  backgroundColor: colors.bg,
  borderRadius: 100,
  borderStyle: "none",
  marginBottom: 10,
  paddingLeft: 20,
};

const buttonStyle = {
  width: "70%",
  backgroundColor: colors.dark,
  borderRadius: 100,
  height: 40,
  borderStyle: "none",
  marginBottom: 10,
};

const buttonStyle2 = {
  width: "100%",
  backgroundColor: colors.dark,
  borderRadius: 100,
  height: 40,
  borderStyle: "none",
  marginBottom: 10,
};

export default ProfilePage;
