import React, { useState } from "react";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    NavbarText
} from 'reactstrap';
import { Link } from "react-router-dom";

export default function UserNavBarComponent(props) {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);
    return (
        <>
            <Navbar  dark expand="md" style={{ backgroundColor: '#EF4B25' }}>
                <NavbarBrand href="/dash/home">WARA DRIVE</NavbarBrand>
                <NavbarToggler onClick={toggle} />
                <Collapse isOpen={isOpen} navbar>
                    <Nav  navbar>
                        
                        <NavItem className="mr-auto">
                            <NavLink href="/auth">HAVE AN ACCOUNT? SIGN IN NOW!</NavLink>
                        </NavItem>

                    </Nav>


                </Collapse>
            </Navbar>
        </>
    );
}
