import React from "react";
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { Link  } from "react-router-dom";

function BookARidePage(props) {

    return (
        <div>
            <h1>BookARide AS guest</h1>
            <ul>
                <li><Link to="/auth/login">Login</Link></li>
                <li><Link to="/auth/sign-up">Sign up</Link></li>
                <li><Link to="/auth/reset-password">Reset Password</Link></li>
                <li><Link to="/guest">Book a ride as a guest</Link></li>
            </ul>
        </div>
      );
}


export default BookARidePage;
