function MyBookingsPage(props) {
  return (
    <Container fluid={true} style={container}>
      <Col xl="10" lg="10" md="10" sm="12" xs="12" style={content}>
        {/* table header */}
        <Row style={titleWrapper}>
          <h5 style={titleText}>Latest Bookings</h5>
          <Button style={titleButton} href="/user/book">
            NEW BOOKING
          </Button>
        </Row>
        {/* bookings table start */}
        {/* table starts */}
        {bookings.length ? (
          <div>
            <Row>
              <Table responsive hover style={{ margin: 0 }}>
                <thead>
                  <tr>
                    <th style={headerText}>Order Ref</th>
                    <th style={headerText}>Service Type</th>
                    <th style={headerText}>Date</th>
                    <th style={headerText}>Status</th>
                  </tr>
                </thead>
                <tbody>
                  {bookings.map((bookings) => {
                    return (
                      <tr>
                        <td>
                          <Button
                            style={orderStyle}
                            href="/user/my-booking-summary"
                          >
                            {bookings.booking.booking_id}
                          </Button>
                        </td>
                        <td>{bookings.booking.stype}</td>
                        <td>{bookings.booking.created.substr(0, 10)}</td>
                        <td>{bookings.booking.status}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </Row>
            <Row style={footerWrapper}>
              <h5 style={titleText}>{""}</h5>
              <Button style={footerButton}>VIEW ALL</Button>
            </Row>
          </div>
        ) : (
          <div>
            <Row style={emptyBooking}>
              <h5>No bookings found</h5>
              <p>click on New Booking to book a ride</p>
            </Row>
          </div>
        )}

        {/* booking table end */}
      </Col>
    </Container>
  );
}

const container = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  marginTop: "3vh",
};

const content = {
  borderStyle: "solid",
  borderWidth: 1,
  borderColor: "rgb(222,226,230)",
  elevation: 10,
};

const titleWrapper = {
  //   borderStyle: "solid",
  padding: 15,
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  backgroundColor: colors.bg,
};

const titleText = {
  fontWeight: 500,
  fontSize: 18,
  margin: 0,
  color: colors.dark,
};

const titleButton = {
  fontSize: 15,
};

const headerText = {
  fontWeight: 550,
  color: colors.dark,
};

const footerWrapper = {
  borderStyle: "solid",
  padding: 10,
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  borderTopWidth: 0.5,
  borderLeftWidth: 0,
  borderRightWidth: 0,
  borderBottomWidth: 0,
  borderColor: "rgb(222,226,230)",
};

const footerButton = {
  backgroundColor: "transparent",
  borderColor: "transparent",
  color: colors.dark,
};

const emptyBooking = {
  display: "grid",
  //   flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  placeItems: "center",
  paddingTop: 50,
  paddingBottom: 50,
};

const orderStyle = {
  backgroundColor: "transparent",
  color: colors.dark,
  borderColor: colors.dark,
};

export default MyBookingsPage;
