function AvailableCarsPage(props) {
  return (
    <div style={content}>
      <Row>
        <Col md="4" style={categoryStyle.wrapper}>
          <FullHeight canExceed className={"col-12"}>
            <Row style={categoryStyle.title}>Available Cars</Row>
            <Row style={categoryStyle.instruction}>
              Click on a category to see available vehicles
            </Row>
            {categories.map((item) => {
              return (
                <Row style={categoryStyle.listWrapper}>
                  <Button style={categoryStyle.button}>{item.name}</Button>
                </Row>
              );
            })}
          </FullHeight>
        </Col>
        <Col>
          {vehicles.length ? (
            <div>
                
            </div>
          ) : (
            <div style={vehiclesStyle.null}>
              <img src={car} alt="null" />

              <h5>No vehicles available found</h5>
              <p>select a category to see available vehicles</p>
            </div>
          )}
        </Col>
      </Row>
    </div>
  );
}

const content = {};

const categoryStyle = {
  wrapper: {
    borderStyle: "solid",
    borderColor: colors.bg,
  },
  title: {
    display: "flex",
    justifyContent: "center",
    textAlign: "center",
    fontSize: 20,
  },
  instruction: {
    fontSize: 12,
    paddingLeft: 10,
    paddingRight: 10,
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    color: "grey",
  },

  listWrapper: {
    display: "flex",
    justifyContent: "center",
    margin: 10,
  },

  button: {
    fontSize: 14,
    width: "100%",
  },
};

const vehiclesStyle = {
  null: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    paddingTop: "20vh",
  },
};

export default AvailableCarsPage;
