import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";

import BookARidePage from '../views/guest/BookARidePage'
import AvailableCarsPage from '../views/guest/AvailableCarsPage'
import ReservationSummaryPage from '../views/guest/ReservationSummaryPage'
import GuestNavBarComponent from '../components/GuestNavBarComponent'

export default function GuestLayout() {
    return (
        <div>
            <GuestNavBarComponent/>

            <Switch>

                <Route path="/guest/book" component={BookARidePage} />
                <Route path="/guest/available-cars" component={AvailableCarsPage} />
                <Route path="/guest/reservation-summary" component={ReservationSummaryPage} />
                <Redirect to="/guest/book" />

            </Switch>
        </div>
    );
}

